function highlightButton(buttons, index) {
  for (let i = 0; i < buttons.length; i++) {
    const button = buttons[i];
    if (index === i) {
      button.classList.add('active');
    } else {
      button.classList.remove('active');
    }
  }
}

function showTabByIndex(tabs, index) {
  for (let i = 0; i < tabs.length; i++) {
    const tabContent = tabs[i];
    tabContent.style.display = index !== i ? 'none' : '';
  }
}

function showTab(e) {
  const button = e.target;
  const buttonIdx = +button.dataset.tabIndex;
  const tabsDiv = button.closest('.gatsby-remark-tabs');

  const tabsContents = tabsDiv.querySelectorAll('.gatsby-remark-tabcontent');
  showTabByIndex(tabsContents, buttonIdx);

  const tabsButtons = tabsDiv.querySelectorAll('.gatsby-remark-tabs-button');
  highlightButton(tabsButtons, buttonIdx);
}

const onRouteUpdate = () => {
  const tabsCollection = document.getElementsByClassName('gatsby-remark-tabs');

  for (let i = 0; i < tabsCollection.length; i++) {
    const tabs = tabsCollection[i];
    const tabsHeaders = tabs.querySelectorAll('.gatsby-remark-tabcontent h3');

    const buttonsDiv = document.createElement('div');
    buttonsDiv.setAttribute('class', 'gatsby-remark-tabs-buttons');

    for (let j = 0; j < tabsHeaders.length; j++) {
      const tabHeader = tabsHeaders[j];
      const button = document.createElement('button');
      button.setAttribute('class', 'gatsby-remark-tabs-button');
      button.setAttribute('data-tab-index', j);
      button.innerText = tabHeader.innerText;
      tabHeader.style.display = 'none';
      button.addEventListener('click', showTab);
      buttonsDiv.append(button);
    }
    tabs.prepend(buttonsDiv);

    const tabsContents = tabs.querySelectorAll('.gatsby-remark-tabcontent');
    showTabByIndex(tabsContents, 0);

    const tabsButtons = tabs.querySelectorAll('.gatsby-remark-tabs-button');
    highlightButton(tabsButtons, 0);
  }
};

exports.onRouteUpdate = onRouteUpdate;
